import { CREATE_ORDER_FAIL, CREATE_ORDER_REQUEST, CREATE_ORDER_SUCCESS,CLEAR_ERRORS, MY_ORDERS_REQUEST, MY_ORDERS_SUCCESS, MY_ORDERS_FAIL, ALL_ORDERS_REQUEST, ALL_ORDERS_SUCCESS, ALL_ORDERS_FAIL, UPDATE_ORDER_REQUEST, DELETE_ORDER_REQUEST, UPDATE_ORDER_SUCCESS, DELETE_ORDER_SUCCESS, UPDATE_ORDER_FAIL, DELETE_ORDER_FAIL, UPDATE_ORDER_RESET, DELETE_ORDER_RESET, ORDER_DETAILS_REQUEST, ORDER_DETAILS_SUCCESS, ORDER_DETAILS_FAIL } from '../constants/orderConstants'

//to create new order
export const createNewOrder = (state = {} , action) =>{
    switch (action.type) {
        case CREATE_ORDER_REQUEST:
            return{
                ...state,
                loading : true
            }

        case CREATE_ORDER_SUCCESS:
            return{
                ...state,
                loading : false,
                order : action.payload
            }

        case CREATE_ORDER_FAIL : 
        return{
            ...state,
            loading : false,
            error : action.payload
        }

        case CLEAR_ERRORS:
            return{
                ...state,
                error : null
            }
    
        default:
            return state
    }
}

//logged in user orders
export const myOrders = (state = {order:[]},action) =>{
    switch (action.type) {
        case MY_ORDERS_REQUEST:
            return{
                ...state,
                loading:true
            }
        
        case MY_ORDERS_SUCCESS:
            return{
                ...state,
                loading:false,
                orders : action.payload
            }

        case MY_ORDERS_FAIL:
            return{
                ...state,
                loading:false,
                error:null
            }
    
        default:
            return state
    }
}

///////////////////////////////////////////////////

//to get all orders by admin
export const allOrders = (state = { orders: [] }, action) => {
    switch (action.type) {
      case ALL_ORDERS_REQUEST:
        return {
          loading: true,
        };
  
      case ALL_ORDERS_SUCCESS:
        return {
          loading: false,
          orders: action.payload,
        };
  
      case ALL_ORDERS_FAIL:
        return {
          loading: false,
          error: action.payload,
        };
  
      default:
        return state;
    }
  };

  //single order details
  export const orderDetails = (state = { orders: {} }, action) => {
    switch (action.type) {
      case ORDER_DETAILS_REQUEST:
        return {
          loading: true,
        };
  
      case ORDER_DETAILS_SUCCESS:
        return {
          loading: false,
          order: action.payload,
        };
  
      case ORDER_DETAILS_FAIL:
        return {
          loading: false,
          error: action.payload,
        };
        
      default:
        return state;
    }
  };

//update status of order & delete order by admin
export const orderAdmin = (state = {}, action) => {
    switch (action.type) {
      case UPDATE_ORDER_REQUEST:
      case DELETE_ORDER_REQUEST:
        return {
          ...state,
          loading: true,
        };
  
      case UPDATE_ORDER_SUCCESS:
        return {
          ...state,
          loading: false,
          isUpdated: action.payload,
        };
  
      case DELETE_ORDER_SUCCESS:
        return {
          ...state,
          loading: false,
          isDeleted: action.payload,
        };
  
      case UPDATE_ORDER_FAIL:
      case DELETE_ORDER_FAIL:
        return {
          ...state,
          loading: false,
          error: action.payload,
        };
      case UPDATE_ORDER_RESET:
        return {
          ...state,
          isUpdated: false,
        };
  
      case DELETE_ORDER_RESET:
        return {
          ...state,
          isDeleted: false,
        };
  
      default:
        return state;
    }
  };


